import React, { useState, useEffect } from 'react';
import { Award, TrendingUp, Loader2, AlertCircle } from 'lucide-react';
import { useAuth } from '../../contexts/AuthContext';
import { useTranslation } from '../../contexts/LanguageContext';
import { apiService } from '../../services/api';
import { extractString, extractFloat, extractInt } from '../../utils/sqlNull';
import type { CourseWithClasses } from '../../types';

interface CourseGradeData {
  enrollmentId: number;
  courseCode: string;
  courseName: string;
  credits: number;
  // Grade components
  aktivitasPartisipatif?: number;
  hasilProyek?: number;
  quiz?: number;
  tugas?: number;
  uts?: number;
  uas?: number;
  nilaiAngka?: number;
  nilaiHuruf?: string;
  nilaiIndeks?: number;
  // Flag to indicate if all grade components are present
  hasCompleteGrades: boolean;
}

const GradesKHS: React.FC = () => {
  const { user, currentSemester } = useAuth();
  const { t } = useTranslation();
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [gradesData, setGradesData] = useState<CourseGradeData[]>([]);

  useEffect(() => {
    const fetchGradesData = async () => {
      if (!user?.studentId || !currentSemester) {
        setError('Student information or semester not available');
        setLoading(false);
        return;
      }

      try {
        setLoading(true);
        setError(null);

        // Fetch enrollments for the student
        const enrollments = await apiService.getEnrollments(user.studentId, currentSemester);

        if (!enrollments || !Array.isArray(enrollments) || enrollments.length === 0) {
          setGradesData([]);
          setLoading(false);
          return;
        }

        // Fetch course details for all enrolled courses
        const courseMap = new Map<string, CourseWithClasses>();
        const coursePromises = enrollments
          .filter(e => extractString(e.kode_mk))
          .map(async (enrollment) => {
            const kode_mk = extractString(enrollment.kode_mk)!;
            try {
              const course = await apiService.getCourseByCode(kode_mk);
              if (course) {
                courseMap.set(kode_mk, course);
              }
            } catch (err) {
              // silently ignore
            }
          });

        await Promise.all(coursePromises);

        // Fetch grades for all enrollments
        const gradePromises = enrollments.map(async (enrollment) => {
          try {
            const grades = await apiService.getGrades({ krs_id: enrollment.id });
            return { enrollment, grade: grades[0] || null };
          } catch (err) {
            return { enrollment, grade: null };
          }
        });

        const enrollmentGrades = await Promise.all(gradePromises);

        // Combine enrollment, course, and grade data
        const combinedData: CourseGradeData[] = enrollmentGrades.map(({ enrollment, grade }) => {
          const courseCode = extractString(enrollment.kode_mk) || '';
          const course = courseMap.get(courseCode);

          // Calculate total SKS (credits)
          const totalSKS = extractInt(course?.sks_tatap_muka) +
                          extractInt(course?.sks_praktek) +
                          extractInt(course?.sks_prak_lapangan) +
                          extractInt(course?.sks_simulasi);

          // Extract grade components
          const aktivitasPartisipatif = grade ? extractFloat(grade.aktivitas_partisipatif) ?? undefined : undefined;
          const hasilProyek = grade ? extractFloat(grade.hasil_proyek) ?? undefined : undefined;
          const quiz = grade ? extractFloat(grade.quiz) ?? undefined : undefined;
          const tugas = grade ? extractFloat(grade.tugas) ?? undefined : undefined;
          const uts = grade ? extractFloat(grade.uts) ?? undefined : undefined;
          const uas = grade ? extractFloat(grade.uas) ?? undefined : undefined;

          // Check if all grade components are present
          const hasCompleteGrades = !!(
            aktivitasPartisipatif !== undefined &&
            hasilProyek !== undefined &&
            quiz !== undefined &&
            tugas !== undefined &&
            uts !== undefined &&
            uas !== undefined
          );

          return {
            enrollmentId: enrollment.id,
            courseCode,
            courseName: course?.nama_mk || courseCode,
            credits: totalSKS,
            aktivitasPartisipatif,
            hasilProyek,
            quiz,
            tugas,
            uts,
            uas,
            nilaiAngka: grade ? extractFloat(grade.nilai_angka) ?? undefined : undefined,
            nilaiHuruf: grade ? extractString(grade.nilai_huruf) ?? undefined : undefined,
            nilaiIndeks: grade ? extractFloat(grade.nilai_indeks) ?? undefined : undefined,
            hasCompleteGrades,
          };
        });

        setGradesData(combinedData);
      } catch (err) {
        setError(err instanceof Error ? err.message : 'Failed to load grades');
      } finally {
        setLoading(false);
      }
    };

    fetchGradesData();
  }, [user?.studentId, currentSemester]);

  // Calculate summary statistics (only for courses with complete grades)
  const coursesWithCompleteGrades = gradesData.filter(g => g.hasCompleteGrades);
  const totalCredits = gradesData.reduce((sum, g) => sum + g.credits, 0);
  const totalCreditsWithGrades = coursesWithCompleteGrades.reduce((sum, g) => sum + g.credits, 0);
  const weightedGPA = coursesWithCompleteGrades.reduce((sum, g) => {
    const indeks = g.nilaiIndeks ?? 0;
    return sum + (g.credits * indeks);
  }, 0);
  const IPS = totalCreditsWithGrades > 0 ? (weightedGPA / totalCreditsWithGrades) : null;

  if (loading) {
    return (
      <div className="max-w-7xl mx-auto flex items-center justify-center py-12">
        <Loader2 className="h-8 w-8 animate-spin text-primary-600" />
        <span className="ml-2 text-gray-600">{t('common.loading')}</span>
      </div>
    );
  }

  if (error) {
    return (
      <div className="max-w-7xl mx-auto">
        <div className="card bg-red-50 border border-red-200">
          <div className="flex items-center space-x-2 text-red-700">
            <AlertCircle className="h-5 w-5" />
            <span>{error}</span>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="max-w-7xl mx-auto space-y-6">
      <div className="flex items-center justify-between">
        <h1 className="text-2xl font-bold text-gray-900">{t('studentGrades.title')}</h1>
        <div className="text-sm text-gray-500">{t('studentGrades.semester')}: {currentSemester}</div>
      </div>

      {/* Summary */}
      <div className="card grid grid-cols-1 md:grid-cols-3 gap-4">
        <div className="flex items-center space-x-3">
          <Award className="h-5 w-5 text-primary-600" />
          <div>
            <div className="text-sm text-gray-500">{t('studentGrades.totalCredits')}</div>
            <div className="text-lg font-semibold">{totalCredits} SKS</div>
          </div>
        </div>
        <div className="flex items-center space-x-3">
          <TrendingUp className="h-5 w-5 text-green-600" />
          <div>
            <div className="text-sm text-gray-500">{t('studentGrades.semesterGPA')}</div>
            <div className="text-lg font-semibold">{IPS !== null ? IPS.toFixed(2) : '-'}</div>
          </div>
        </div>
        <div className="flex items-center space-x-3">
          <Award className="h-5 w-5 text-amber-600" />
          <div>
            <div className="text-sm text-gray-500">{t('studentGrades.status')}</div>
            <div className="text-lg font-semibold">
              {IPS !== null ? (IPS >= 3.0 ? t('studentGrades.goodStanding') : IPS >= 2.0 ? t('studentGrades.warning') : t('studentGrades.probation')) : '-'}
            </div>
          </div>
        </div>
      </div>

      {/* Grades Table */}
      {gradesData.length === 0 ? (
        <div className="card text-center py-12 text-gray-500">
          {t('studentGrades.noGrades')}
        </div>
      ) : (
        <div className="card overflow-x-auto">
          <table className="min-w-full text-sm">
            <thead>
              <tr className="text-left text-gray-600 border-b-2 border-gray-200">
                <th className="py-3 px-2">{t('studentGrades.courseCode')}</th>
                <th className="py-3 px-2">{t('studentGrades.courseName')}</th>
                <th className="py-3 px-2 text-center">{t('studentGrades.credits')}</th>
                <th className="py-3 px-2 text-center">{t('studentGrades.participation')}</th>
                <th className="py-3 px-2 text-center">{t('studentGrades.project')}</th>
                <th className="py-3 px-2 text-center">{t('studentGrades.quiz')}</th>
                <th className="py-3 px-2 text-center">{t('studentGrades.assignment')}</th>
                <th className="py-3 px-2 text-center">{t('studentGrades.uts')}</th>
                <th className="py-3 px-2 text-center">{t('studentGrades.uas')}</th>
                <th className="py-3 px-2 text-center">{t('studentGrades.total')}</th>
                <th className="py-3 px-2 text-center">{t('studentGrades.grade')}</th>
                <th className="py-3 px-2 text-center">{t('studentGrades.index')}</th>
              </tr>
            </thead>
            <tbody>
              {gradesData.map((g) => (
                <tr key={g.enrollmentId} className="border-t border-gray-100 hover:bg-gray-50">
                  <td className="py-3 px-2 font-medium text-gray-900">{g.courseCode}</td>
                  <td className="py-3 px-2">{g.courseName}</td>
                  <td className="py-3 px-2 text-center">{g.credits}</td>
                  <td className="py-3 px-2 text-center">{g.aktivitasPartisipatif?.toFixed(1) ?? '-'}</td>
                  <td className="py-3 px-2 text-center">{g.hasilProyek?.toFixed(1) ?? '-'}</td>
                  <td className="py-3 px-2 text-center">{g.quiz?.toFixed(1) ?? '-'}</td>
                  <td className="py-3 px-2 text-center">{g.tugas?.toFixed(1) ?? '-'}</td>
                  <td className="py-3 px-2 text-center">{g.uts?.toFixed(1) ?? '-'}</td>
                  <td className="py-3 px-2 text-center">{g.uas?.toFixed(1) ?? '-'}</td>
                  <td className="py-3 px-2 text-center font-medium">
                    {g.nilaiAngka?.toFixed(1) ?? '-'}
                  </td>
                  <td className="py-3 px-2 text-center font-semibold text-primary-600">
                    {g.hasCompleteGrades ? (g.nilaiHuruf ?? '-') : '-'}
                  </td>
                  <td className="py-3 px-2 text-center">
                    {g.hasCompleteGrades ? (g.nilaiIndeks?.toFixed(2) ?? '-') : '-'}
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      )}
    </div>
  );
};

export default GradesKHS;
